import React, {useEffect, useState} from 'react'
import jQuery from "jquery";
window.$ = window.jQuery = jQuery

var IMP = window.IMP; 
IMP.init("imp10942072"); 

export const PaymentPage = () => {
  const [numOfMember, setNumOfMember] = useState(0)
  const [sum, setSum] = useState(0)
  const [name] = useState("1년 이용권")
  
  const callPaymentModule = () => {
    IMP.request_pay({ // param
      pg: "html5_inicis.INIBillTst",
      pay_method: "card",
      name: name,
      merchant_uid: `${name}_${new Date().getTime()}`,
      amount: 100,
      buyer_email: "naver@gmail.com",
      buyer_name: "받는 페이지",
      buyer_tel: "010-4242-4242", 
      buyer_addr: "서울특별시 강남구",
      buyer_postcode: "01181",
    }, function(rsp){
      console.log(rsp)
    }

    )} 
/**
 * 결제금액 계산하는 useEffect
 */
    useEffect(() => {
    setSum(numOfMember * 6000)
    }, [numOfMember])

    return (
        <div>
      <header className="App-header">
        <label>몇명의 인원이 사용하나요?</label>
        <input placeholder="10" onChange={(e)=>setNumOfMember(e.nativeEvent.data)} value={numOfMember}/>
        <label>결제주기를 선택해주세요</label>
        <input type="text" name="name" />

        <p>결제하실 총 금액은 {sum}</p>
        <p></p>
        <button onClick={callPaymentModule}>
          결제
        </button>
      </header>
    </div>
    )
}

export default PaymentPage